import React from "react";
import { TextField, Button, Typography } from "@mui/material";

function LoginPage(props) {
  const { register, errors } = props;
  return (
    <>
      <Typography variant="h2" fontFamily="Jacquard" textAlign="center">
        
      </Typography>
      <Typography
        variant="body1"
        marginBottom={1}
        fontFamily="Jacquard"
        textAlign="center"
      >
            
      </Typography>
      <TextField
        error={!!errors.email}
        fullWidth={true}
        margin="normal"
        label="Email"
        variant="outlined"
        placeholder="  e-mail"
        helperText={errors.email ? errors.email.message : ""}
        {...register("email")}
      />
      <TextField
        error={!!errors.password}
        type="password"
        fullWidth={true}
        margin="normal"
        label="Password"
        variant="outlined"
        placeholder="  "
        helperText={errors.password ? errors.password.message : ""}
        {...register("password")}
      />
      <Button
        type="submit"
        sx={{
          fontFamily: "Jacquard",
          marginTop: 2,
          width: "60%",
          marginBottom: 2,
        }}
        variant="contained"
      >
        
      </Button>
      <Typography variant="body1" sx={{ fontFamily: "Jacquard" }}>
           ?<span className="incitingText"></span>
      </Typography>
    </>
  );
}

export default LoginPage;
